import { Component } from '@angular/core';
import {AlertController, IonicPage,ModalController, NavController, NavParams, InfiniteScroll } from 'ionic-angular';
import { Auth ,NotificationProvider} from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';




@IonicPage()
@Component({
  selector: 'page-reglamentod-list',
  templateUrl: 'reglamentod-list.html',   
})
export class ReglamentodListPage {

  public loading: boolean = true;
 public items: any=[];
  items2: any=[];
  tiposus: any=[];

    generated = '';
 public llave:any=[];
  public gLimit: number = 50;
  public PageSegmet: string = 'foods';
  public gSearchQuery: string = '';
  username: string = "";
  titulox:any;
  unidadt:any;
  categoria:any;
  pingreso:boolean=false;
  pconsulta:boolean=false;
  peliminacion:boolean=false;
  pcreacion:boolean=false;
  pactualizacion:boolean=false;
  preporte:boolean=false;
  pfiltro:boolean=false;
  permisom: any=[];
  idempresa:any;
  idafiliado:any='na';

//private barcodeScanner:BarcodeScanner,
  constructor( public alertCtrl: AlertController,public storage: Storage, public msj: NotificationProvider, public translateService: TranslateService
    ,public navCtrl: NavController,
    private _MODAL  		: ModalController, public navParams: NavParams,
     public regService: Auth
 

    ) {

      this.permisom = String(navParams.get("permiso")).split(",");
      this.pingreso=(  this.permisom[0] == 'true');
      this.pconsulta=(  this.permisom[1] == 'true');
      this.peliminacion=(  this.permisom[2] == 'true');
      this.pcreacion=(  this.permisom[3] == 'true');
      this.pactualizacion=(  this.permisom[4] == 'true');
      this.pfiltro=(  this.permisom[5] == 'true');
      this.preporte=(  this.permisom[6] == 'true');
      this.translateService.get('APPX').subscribe((value) => {
        this.idempresa=value
       
    })
     
  }
  
  public clearSearchg(): void {
    this.gSearchQuery = '';
  }
  showFilter()     
  {


    
  }


  
  pideregistros() { 
    let self = this;
    self.loading = true;   
    this.regService.getReg(this.navParams.data.idempresa +'/'+ this.categoria+'/todos','reglamentods').then((data) => {
      this.items=data;
      self.loading = false;
}, (err) => {
  this.msj.showError3('Error',err.error);

});

   
 
        
              
      }
      
  

      
  
  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  }); 
  }
  ionViewDidLoad() {
    this.categoria=this.navParams.data.record._id;
    this.titulox=this.navParams.data.titulo;




    this.storage.get('tiposus').then((value) => {
      this.tiposus = value.split(',');
    this.unidadt=this.tiposus[7]

    this.pideregistros();

  
  });


    
   
  }
  
  public loadMore(ev: InfiniteScroll) {
    this.gLimit += 50;
    setTimeout(() => ev.complete(), 1000);
  }




  deleteRecord(item : any, no) : void
  {
    let alert = this.alertCtrl.create({ title: 'Esta seguro de realizar esta operaciÃ³n?',
    buttons: [
      {text: 'Si',
        handler: () => {
              
      
    let recordID 		: string		= item._id;
    this.regService.deleteReg(recordID+'/'+this.username+ '/' + this.idempresa + '/' + this.idafiliado,'reglamentods').then((data) => {
        this.items.splice(no, 1);
        this.items = this.items.concat([]);
      }, (err) => {
        this.msj.showError3('Error',err.error);
    });
      
        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]
  });
  alert.present();
     
  
  }
  value:any;

  updateRecord2(item : any,no) : void
   {
    let addModal = this._MODAL .create('ReglamentodCreatePage', { record : item,titulo:this.titulox,idempresa:this.navParams.data.idempresa,idcategoria:this.categoria,dataxx:this.navParams.data.record},{showBackdrop: true,enableBackdropDismiss: false,cssClass: "modal-fullscreen"});
    addModal.onDidDismiss(data => {
      if (data) {
        this.pideregistros()
      }
    })
    addModal.present();
    //  this.navCtrl.push('EmpresaCreatePage', { record : item });
   }

 

  updateRecord(item : any,no) : void
   {
    let addModal = this._MODAL .create('ReglamentodCreatePage', { record : item,titulo:this.titulox,idempresa:this.navParams.data.idempresa,idcategoria:this.categoria,dataxx:this.navParams.data.record},{showBackdrop: true,enableBackdropDismiss: false,cssClass: "modal-fullscreen"});
    addModal.onDidDismiss(data => {
      if (data) {
        this.pideregistros()
      }
    })
    addModal.present();
    //  this.navCtrl.push('EmpresaCreatePage', { record : item });
   }
   addRecord() : void
   {
    let addModal = this._MODAL .create('ReglamentodCreatePage',{ record : null,titulo:this.titulox,idempresa:this.navParams.data.idempresa,idcategoria:'123',dataxx:this.navParams.data.record},{showBackdrop: true,enableBackdropDismiss: false,cssClass: "modal-fullscreen"});
    addModal.onDidDismiss(data => {
      if (data) {
        this.pideregistros()
      }  
    })
    addModal.present();

      //this.navCtrl.push('EmpresaCreatePage');
   }
 




}



